<template>
  <a class="logo" :href="link">
    <img :src="require(`@/assets/${imagePath}`)" :alt= "imageAlt"/>
    <svg width="100%" height="100%" viewbox="0 0 100 100" preserveAspectRatio="none">
      <circle cx="30" cy="26" r="20" stroke="#417a94" stroke-width="5" fill="none"/>
    </svg>
  </a>
</template>

<script>
export default {
  name: 'NavBarButton',
  props: {
    link: {
        type: String,
        required: true,
    },
    imagePath: {
        type: String,
        required: true,
    },
    imageAlt: {
        type: String,
        required: true,
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.logo{
    color: black;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    font-size: 15px;
    height: 60px;
    width:60px;
    position: absolute;
    top: 6px;
    left:40px;
    display: inline-block; /* <= shrinks container to image size */
}

.logo img{
  display: block;
  max-width: 100%;
  height: auto;
}

.logo svg {
  position: absolute;
  top: 0;
  left: 0px;
}

.logo:hover{
  animation-name: contFade;
  animation-duration: 4s;
  animation-iteration-count:infinite;
}

.logo svg:hover{
  color: #451e3e;
}

@keyframes contFade {
  0%, 100%{
    opacity:1;
  }
  50%{
    opacity: .6;
  }
}
</style>
